<template>
    <div class="workFinish" >
        <navigation-bar
          pageName="工单完成"
          @onLeftClick="goBack"/>
        <div class="box">
            <div class="field-box">
                <van-field
                    clickable
                    label="责任单位"
                    input-align="right"
                    placeholder="请选择责任单位"
                    v-model="filterCompany"
                    :right-icon="queryIcon"
                    @click-right-icon="getCompanyList"/>
                <div class="show-list" v-show="companyName.length > 0">
                    <div v-for="(item,index) in companyName" :key="index">
                        {{item}}
                    </div>
                </div>
                <van-field
                    clickable
                    label="施工单位"
                    input-align="right"
                    placeholder="请选择施工单位"
                    v-model="filterConstruction"
                    :right-icon="queryIcon"
                    @click-right-icon="getConstructionList"/>
                <div class="show-list" v-show="constructionName.length > 0">
                    <div v-for="(item,index) in constructionName" :key="index">
                        {{item}}
                    </div>
                </div>
            </div>
            <div class="area-box">
                <textarea name="" id="" placeholder="请填写完成情况..." v-model="workContent" maxlength="100" @input="filterContent"></textarea>
                <div class="num">{{workContent.length}}/100</div>
            </div>
            <div class="content">
                <!--<div class="visible-box">
                    <div class="title">业主是否可见</div>
                    <van-switch v-model="isVisiable" size="24px" active-color="#00d8d1" inactive-color="#FFFFFF"/>
                </div>-->
                <div class="photo-box">
                    <div class="title">
                        <div>上传图片</div>
                        <div class="limit">(最多4张)</div>
                    </div>
                    <div class="photo">
                        <add-image v-on:imageAddAction="showAddSheetControll" v-on:imageMapChange="upDateImageData" ref="addImageComponent"></add-image>
                    </div>
                </div>
            </div>
        </div>

        <div class="submit-box">
          <div class="submit" @click="finish">完成</div>
        </div>
        <van-popup v-model="showCondition" position="bottom">
          <div class="checkbox-box">
            <div class="checkbox-group">
              <van-checkbox-group v-model="conditionCodeList" @change="toggleValue">
                <van-checkbox v-for="(item, index) in showList" :key="index" checked-color="#00d8d1" :name="item.CONSTRU_CODE" >
                {{item.CONSTRU_NAME}}
                </van-checkbox>
              </van-checkbox-group>
            </div>
            <div class="checkbox-submit" @click="show">确定</div>
          </div>
        </van-popup>

        <van-action-sheet
          v-model="showAddSheet"
          :actions="actions"
          cancel-text="取消"
          round
          close-on-click-action
          @select="addSelect"/>
    </div>
</template>

<script>
import {
  finishWork,
  getCompanyOrConstructionList
} from "../../services/internalWorkPlanService";
import { store } from "../../utils/store";
import AddImageVue from "../addImage/AddImage.vue";
import queryIcon from '@/assets/imgs/workPlan/query.png';
import NavigationBar from "../title/NavigationBar";
export default {
  name: "workFinish",
  data() {
    return {
      isVisiable: false,
      workContent: "",

      showCondition: false,

      // 选中的值
      conditionCodeList: [],
      conditionNameList: [],

      // 责任单位还是施工单位
      selectType: 0,
      // 弹窗展示的列表
      showList: [],
      filterCompany: "",
      filterConstruction: "",

      // 展示的责任单位列表和施工单位列表
      companyCode: [],
      companyName: [],
      constructionCode: [],
      constructionName: [],
      workDetail: null,
      userInfo: null,

      actions: [{ name: "拍照" }, { name: "从手机相册选择" }],
      imgList: [],
      showAddSheet: false,

      queryIcon: queryIcon
    };
  },
  components: {
    "add-image": AddImageVue,
    NavigationBar
  },
  created() {
    this.workDetail = this.$route.params;
    this.userInfo = store.get("userInfo");
  },
  methods: {
    /**
     * 页面返回事件
     */
    goBack: function() {
      this.$router.back(-1);
    },

    /**
     * 获取责任单位
     */

    getCompanyList: function() {
      this.selectType = 0;
      var data = {
        AREA_CODE: this.workDetail.areaCode
      };
      if (this.filterCompany) {
        data.DUTY_CODE = this.filterCompany;
      }
      getCompanyOrConstructionList({ data: JSON.stringify(data) }).then(
        res => {
          this.showList = res;
          this.conditionCodeList = this.companyCode.concat([]);
          this.conditionNameList = this.companyName.concat([]);
          this.showCondition = true;
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 获取施工单位
     */
    getConstructionList: function() {
      if(this.companyCode.length <= 0 ){
        this.$toast("请先选择责任单位！");
      }
      this.selectType = 1;
      var data = {
        AREA_CODE: this.workDetail.areaCode
      };
      if (this.filterConstruction) {
        data.CONSTRU_CODE = this.filterConstruction;
      }
      getCompanyOrConstructionList({
        data: JSON.stringify(data)
      }).then(
        res => {
          this.showList = res;
          this.conditionCodeList = this.constructionCode.concat([]);
          this.conditionNameList = this.constructionName.concat([]);
          this.showCondition = true;
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * checkbox切换值时触发
     */
    toggleValue: function() {
      this.conditionNameList = [];
      this.showList.forEach(item => {
        this.conditionCodeList.forEach(item2 => {
          if (item.CONSTRU_CODE == item2) {
            this.conditionNameList.push(item.CONSTRU_NAME);
          }
        });
      });
    },

    /**
     * 添加单位或者删除单位
     */
    show: function() {
      this.showCondition = false;
      if (this.selectType == 0) {
        this.companyCode = this.conditionCodeList.concat([]);
        this.companyName = this.conditionNameList.concat([]);
      }
      this.constructionCode = this.conditionCodeList.concat([]);
      this.constructionName = this.conditionNameList.concat([]);
    },

    /**
     * 子组件更新已选图片集合监听函数
     **/
    upDateImageData(newImageData){
      this.imgList = newImageData;
    },

    /**
     * addImage组件添加照片时的监听函数父组件用来选择哪种图片获取方式(拍照或相册上传)
     **/
    showAddSheetControll() {
      this.showAddSheet = !this.showAddSheet;
    },

    /**
     * 选择当前图片来源方式(拍照或本地上传)
     * @param
     * item      Object   当前选择的方式对象
     * index     Number   当前选择的索引
     **/
    addSelect(item, index) {
      this.getPictureData(index);
    },

    /**
     * 调用摄像头或者本地相册进行图片上传
     * @param
     **/
    getPictureData(selectIndex) {
      selectIndex?this.$refs.addImageComponent.getPicture():this.$refs.addImageComponent.useCamera();
    },

    /**
     * 禁止输入表情
     */
    filterContent: function() {
      this.workContent = this.workContent.replace(
        /[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF][\u200D|\uFE0F]|[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF]|[0-9|*|#]\uFE0F\u20E3|[0-9|#]\u20E3|[\u203C-\u3299]\uFE0F\u200D|[\u203C-\u3299]\uFE0F|[\u2122-\u2B55]|\u303D|[\A9|\AE]\u3030|\uA9|\uAE|\u3030/gi,
        ""
      );
    },

    /**
     * 完成点击事件
     */
    finish: function() {
      if (!this.hasCompleteParam()) {
        return;
      }
      var finishData = {
        complCode: this.workDetail.complCode,
        areaCode: this.workDetail.areaCode,
        roomCode: this.workDetail.roomCode,
        construCode: this.companyCode.join(","),
        dutyCode: this.constructionCode.join(","),
        createdBy: this.userInfo.USER_CODE,
        userCode: this.userInfo.USER_CODE,
        picUrl: this.imgList,
        content: this.workContent,
        isShow: this.isVisiable ? 1 : 0
      };
      finishWork(finishData).then(
        res => {
          if (res.resultCode == "0") {
            this.$toast("完成成功");
            store.set('insideWorkDetailComplCode', this.workDetail.complCode);
            this.goBack();
          } else {
            this.$toast(res.msg);
          }
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 判断是否必选的参数填写完
     */
    hasCompleteParam: function() {
      if (this.companyCode.length <= 0) {
        this.$toast("请选择责任单位！");
        return false;
      }
      if (!this.workContent) {
        this.$toast("请填写完成信息！");
        return false;
      }
      // if (this.imgList.length <= 0) {
      //   this.$toast("请上传完成图片！");
      //   return false;
      // }
      return true;
    }
  },
  computed: {}
};
</script>
